* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
  display: flex;
}

img {
  max-width: 100%;
  display: block;
}

.GoBack {
  display: inline-block;
  position: absolute;
  top: 20px;
  left: 20px;
}

.box-input {
  justify-content: space-between;
  margin-top: 50px;
  margin-bottom: 50px;
  display: flex;
}

.flatpickr-input {
  display: block;
}

.btnGoBack {
  background: #1486e4;
  border-radius: 2px;
  padding: 12px;
  text-decoration: none;
  display: inline-block;
}

.timer {
  color: #f7e3e3;
  font-family: sans-serif;
  font-weight: 100px;
  text-align: center;
  font-size: 30px;
  display: inline-block;
}

.field {
  background: #0d7ec9;
  border-radius: 2px;
  padding: 12px;
  display: inline-block;
}

.value {
  background: #5eacff;
  border-radius: 2px;
  padding: 18px;
  display: inline-block;
}

.label {
  padding-top: 5px;
  font-size: 16px;
  display: block;
}

.flatpickr-input {
  width: 200px;
  text-align: center;
  color: #f7e3e3;
  background: #5eacff;
  border-radius: 3px;
  outline: none;
  margin-right: 50px;
  font-family: Roboto;
  font-size: 20px;
  display: flex;
}

.btn {
  color: #f7e3e3;
  cursor: pointer;
  background: gray;
  border: none;
  border-radius: 3px;
  padding: 5px 20px;
  font-size: 26px;
}

.nx-message {
  position: relative;
}

